<template>
  <div class="wrapper wrapper-content">

    <div class="ibox float-e-margins">
      <div class="ibox-title">
        <h5>用户详情</h5>
      </div>

      <div class="ibox-content">

        <form class="form-horizontal" @submit.prevent="submit" id="form">

          <div class="form-group">
            <label class="control-label"><span class="MustFill">*</span>登录名</label>
            <div>
              <input type="text" class="form-control" required="" aria-required="true"
                     name="username" v-model="form.username" :disabled="id != 'new'">
            </div>
          </div>

          <div class="form-group" v-if="form.username != 'admin'">
            <label class="control-label"><span class="MustFill">*</span>用户组</label>
            <div>
              <select class="form-control" required="" aria-required="true"
                      name="role" v-model="form.r_id">
                <option v-for="role of roles" :value="role.id">{{ role.name }}</option>
              </select>
            </div>
          </div>

          <div class="form-group">
            <label class="control-label"><span class="MustFill">*</span>用户名称</label>
            <div>
              <input type="text" class="form-control" required="" aria-required="true" name="name" v-model="form.name">
            </div>
          </div>

          <div class="form-group">
            <label class="control-label">联系电话</label>
            <div>
              <input type="text" maxlength="11" class="form-control" name="phone" v-model="form.phone" onkeyup="this.value=this.value.replace(/[^0-9a-zA-Z]/g,'')">
            </div>
          </div>

          <div class="form-group" v-if="id == 'new'">
            <label class="control-label"><span class="MustFill">*</span>密码</label>
            <div>
              <input type="password" class="form-control" required="" aria-required="true" name="password" v-model="form.password" onkeyup="this.value=this.value.replace(/[^0-9a-zA-Z]/g,'')">
            </div>
          </div>

          <div class="form-group">
            <label class="control-label"><span class="MustFill">*</span>邮箱</label>
            <div>
              <input type="email" class="form-control" required="" aria-required="true" name="email" v-model="form.email">
            </div>
          </div>


          <div class="text-center">
            <button type="submit" class="btn btn-primary">保存</button>
            <b class="btn btn-white" @click="cancel">取消</b>
          </div>
        </form>
      </div>
    </div>

  </div>
</template>

<script type="text/ecmascript-6">
import Cache from '@/assets/cache'
import tabsService from '@/assets/template/tabs'
import dataService from '@/assets/method/data'
import utilizeService from '@/assets/template/utilize'
import Service from '@/assets/service/service'
export default {
  name: 'UserDetails',
  data () {
    return {
      id: this.$route.params['id'],
      roles: [],
      form: {},
      permissions: Cache.getUser('permission'),
      isSuper: $.inArray('admin', this.permissions) > -1 ? true : false,
      canEditRole: false, // 是否有修改用户分组的权限
      validate: null
    }
  },
  methods: {
    submit () {
      if (!$('#form').valid()) {
        toastr.warning('请补贴提交数据')
        return
      }
      if (this.form.phone && this.form.phone.length != 11) {
        toastr.warning('请输入11位数的电话号码')
        return
      }
      if (this.form.password) {
        if (this.form.password == Number(this.form.password) || this.form.password.length < 6) {
          toastr.warning('请输入6位以上的字母加数字')
          return
        }
      }

      const request = {

      }
      for (const key in this.form) {
        if (key == 'r_id') {
          if (this.form[key] != 0) {
            request[key] = this.form[key]
          }
        } else {
          if ($.inArray(key, ['email', 'name', 'username', 'password', 'phone']) > -1) {
            request[key] = this.form[key]
          }
        }
      }
      if (this.id == 'new') {
        Service.Users.add(request).then(response => this.responseHandle(response))
      } else {
        Service.Users.edit(this.id, request).then(response => this.responseHandle(response))
      }
    },
    responseHandle (response) {
      if (response.code == 200) {
        toastr.success(response.msg)
        setTimeout(() => dataService.tabReload(602), 1000)
      } else {
        toastr.error(response.msg)
        this.canSubmit = true
      }
    },
    cancel () {
      tabsService.closeNowTab()
    }
  },
  created () {
    if (this.isSuper) {
      this.canEditRole = true
    }
    Service.Roles.get().then(response => {
      if (response.code == 200) {
        this.roles = response.data
      }
    })
  },
  mounted () {
    this.validate = dataService.validate('#form') // 表单验证
    if (this.id != 'new') {
      Service.Users.details(this.id).then(response => {
        if (response.code == 200) {
          this.form = response.data
          this.form.r_id = response.data.roles[0].id
        }
      })
    }
  }
}
</script>

<style scoped>
  .btn {
    margin-bottom: 0;
  }
  form {
    width: 400px;
    margin: 0 auto;
  }
  .form-group label {
    display: inline-block;
    width: 120px;
    float: left;
  }
  .form-group div{
    display: inline-block;
    width: 250px;
    margin-left: 20px;
    float: left;
  }
</style>



